:root {
  --color-light: #fff;
  --color-dark: #000;
  --color-accent: #4AB45E;
  --color-title: #14183E;
  --color-secondary: #F5F5F5;
  --color-hero-bg: #23373a;
  --padding-container: 15px;
  --padding-section: 4.6875em;
  --offset-page: 1280px;
  --offset-header: 0px;
}

@font-face {
  font-family: "RobotoMy";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "InterMy";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "InterMy";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "InterMy";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "InterMy";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}
@media (max-width: 576px) {
  html {
    font-size: 12px;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  font-family: "InterMy", sans-serif;
  font-weight: 400;
  color: var(--color-dark);
}

.container {
  max-width: calc(var(--offset-page) + var(--padding-container) * 2);
  padding: 0 var(--padding-container);
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.sr-only {
  clip: rect(0 0 0 0); /* Это свойство устарело*/
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
}

.padding-section {
  padding: var(--padding-section) 0;
}

/* svg, video, img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
} */
.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.title-section {
  font-weight: 600;
  font-size: 1.125rem;
  line-height: 1.5em;
  color: var(--color-accent);
}

.title {
  display: block;
  font-weight: 700;
  font-size: 2.875rem;
  line-height: 1.217em;
  text-transform: capitalize;
  color: var(--color-title);
}

.nav-header__list {
  display: flex;
}
.nav-header__item:not(:last-child) {
  margin-right: 3.125em;
}
.nav-header__link {
  font-weight: 500;
  font-size: 1.125rem;
  color: var(--color-light);
  transition: color 0.3s ease-in-out;
}
.nav-header__link:hover {
  color: var(--color-accent);
}

@media (max-width: 992px) {
  .nav-header {
    position: fixed;
    top: 0;
    right: 0;
    min-width: 300px;
    height: 100vh;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, 0.9);
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    z-index: 100;
  }
  .nav-header__list {
    display: block;
    padding-top: var(--offset-header);
  }
  .nav-header__item {
    text-align: center;
    padding: 20px 0;
  }
  .nav-header__item:not(:last-child) {
    margin-right: 0;
  }
  .nav-header.nav--active {
    transform: translateX(0%);
  }
}
@media (max-width: 360px) {
  .nav-header {
    min-width: 100%;
  }
}
.btn {
  border: 1px solid var(--color-accent);
  background-color: var(--color-accent);
  color: var(--color-light);
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.btn:hover {
  border: 1px solid var(--color-light);
  background-color: transparent;
  color: var(--color-light);
}

.btn-item {
  border: 1px solid var(--color-accent);
  background-color: transparent;
  color: var(--color-accent);
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.btn-item:hover {
  border: 1px solid var(--color-accent);
  background-color: var(--color-accent);
  color: var(--color-light);
}

@media (min-width: 0px) {
  .hero-swiper.swiper {
    max-width: 992px;
    width: 100%;
    height: 410px;
  }
  .hero__swiper-slide.swiper-slide {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding: 14px 17px 21px 17px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.05);
    box-sizing: border-box;
    backdrop-filter: blur(93.4834px);
  }
  .hero-swiper__img {
    width: 100%;
    height: 100%;
    border-radius: inherit;
    overflow: hidden;
    margin-bottom: 18px;
  }
  .hero-swiper__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .hero-swiper__title {
    font-family: "InterMy";
    font-weight: 600;
    font-size: 1.125rem;
    line-height: 1, 222em;
    color: var(--color-light);
  }
  .swiper-controls-button {
    position: relative;
    padding-bottom: 20px;
    width: 100%;
    margin-top: 10px;
  }
  .swiper-controls-button > .swiper-button-prev:after,
.swiper-controls-button > .swiper-button-next:after {
    content: "";
    width: 100%;
    height: 100%;
    background-color: var(--color-accent);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 8.17px 16.33px;
    transition: background-color 0.3s ease-in-out;
  }
  .swiper-controls-button > .swiper-button-prev:after {
    background-image: url("../img/Prev.svg");
  }
  .swiper-controls-button > .swiper-button-next:after {
    background-image: url("../img/Next.svg");
  }
  .swiper-controls-button > .swiper-button-next.swiper-button-disabled,
.swiper-controls-button > .swiper-button-prev.swiper-button-disabled {
    opacity: 0.6;
  }
  .swiper-controls-button > .swiper-button-prev.swiper-button-disabled.swiper-button-prev:after,
.swiper-controls-button > .swiper-button-next.swiper-button-disabled.swiper-button-next:after {
    background-color: rgba(255, 255, 255, 0.3);
  }
  .swiper-controls-button > .swiper-button-next,
.swiper-controls-button > .swiper-button-prev {
    top: 0;
    width: 60px;
    height: 52px;
    transform: translateY(50%);
    border-radius: 20px;
    overflow: hidden;
  }
  .swiper-controls-button > .swiper-button-prev,
.swiper-controls-button > .swiper-rtl .swiper-button-next {
    right: 95px;
    left: auto;
  }
  .swiper-controls-button > .swiper-button-next,
.swiper-controls-button > .swiper-rtl .swiper-button-prev {
    right: 27px;
  }
}
@media (min-width: 360px) {
  .swiper-slide {
    max-width: 310px;
  }
}
@media (min-width: 1200px) {
  .hero-swiper.swiper {
    max-width: 100%;
  }
  .hero__swiper-slide.swiper-slide {
    max-width: 310px;
  }
}
.burger-menu {
  position: relative;
  display: none;
  width: 50px;
  height: 15px;
  overflow: hidden;
  z-index: 1000;
}
.burger-menu::before, .burger-menu::after {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 3px;
  background-color: white;
}
.burger-menu::before {
  top: 0;
}
.burger-menu::after {
  bottom: 0;
  transform: translateX(50%);
  transition: transform 0.3s ease-in-out;
}

.burger-menu.menu--active {
  position: fixed;
  right: 15px;
}

.burger-menu.menu--active.burger-menu::after {
  transform: translateX(0%);
}

@media (max-width: 992px) {
  .burger-menu {
    display: block;
  }
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 0;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 992px) {
  .header {
    padding: 20px 0;
  }
}
.hero {
  background-image: url("../img/hero_bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: var(--color-hero-bg);
  padding-top: calc(var(--offset-header) + 4.18em);
  padding-bottom: 57px;
  display: flex;
}
.hero__left {
  max-width: calc(617px + (100% - var(--offset-page)) / 2);
  margin-right: 57px;
  color: var(--color-light);
  padding-left: max(var(--padding-container), (100% - var(--offset-page)) / 2);
}
.hero__title {
  font-weight: 700;
  font-size: 3.5rem;
  line-height: 1.5714em;
  max-width: 527px;
  margin: 0;
  margin-bottom: 0.1785em;
}
.hero__descr {
  font-size: 1.125rem;
  line-height: 2.6666em;
  margin-bottom: 1.6666em;
  width: 100%;
}
.hero__btn {
  font-family: "InterMy";
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.2222em;
  padding: 1.1111em 1.9444em;
  border-radius: 0.2222em;
}
.hero__right {
  flex-shrink: 1;
  width: 100%;
}

@media (max-width: 1199px) {
  .hero {
    display: block;
  }
  .hero__left {
    max-width: 100%;
    margin-right: 0;
    padding-left: 0;
  }
  .hero__title {
    max-width: 727px;
    text-align: center;
    margin: 0 auto 10px;
    padding: 0 15px;
  }
  .hero__descr {
    text-align: center;
    margin: 0 auto 10px;
    width: 790px;
    padding: 0 15px;
  }
  .hero__btn {
    display: block;
    margin: 0 auto;
    margin-bottom: 30px;
  }
  .hero__right {
    flex-shrink: 1;
    width: 100%;
  }
}
@media (max-width: 991px) {
  .hero__descr {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .hero__title {
    max-width: 527px;
    padding: 0 15px;
  }
}
.about__container {
  display: flex;
  gap: 47px;
}
.about__content {
  flex: 1 1 100%;
  max-width: 613px;
}
.about__title-section {
  margin-bottom: 10px;
}
.about__title {
  margin-bottom: 21px;
}
.about__descr {
  line-height: 1.875em;
  text-align: justify;
  opacity: 0.5;
  text-transform: capitalize;
}
.about__descr:not(:last-child) {
  margin-bottom: 20px;
}
.about__image {
  margin-top: 20px;
  flex: 1 1 100%;
}

.anim-svg {
  max-width: 675px;
  max-height: 400px;
  overflow: hidden;
}
.anim-svg__window {
  width: 100%;
  height: 100%;
}

@media (max-width: 1200px) {
  .about__container {
